import React from "react";
import LogIn from "./components/LogIn";
import SignUp from "./components/SignUp";
import Dashboard from "./components/Dashboard";
import { BrowserRouter as Router, Route } from "react-router-dom";

function App() {


  if (window.location.pathname !== '/password-manager/') {
    if (window.location.pathname !== '/password-manager/login') {
      if (window.location.pathname !== '/password-manager/signup') {
        if (window.location.pathname !== '/password-manager/dashboard') {
          window.location.assign('/')
        }
      }
    }
  }
  return (
    <Router>
      <div className="wrapper">  
        <Route path="/password-manager/" exact component={LogIn} />
        <Route path="/password-manager/login" exact component={LogIn} />
        <Route path="/password-manager/signup" exact component={SignUp} />
        <Route path="/password-manager/dashboard" exact component={Dashboard} />
      </div>
    </Router>
  );
}

export default App;
